/* Menu */

#nav {
  display: inline-block;
}

@media screen and (max-width: 980px) {
  #nav {
    display: none;
  }
}

.navPanelToggle {
  position: fixed;
  text-decoration: none;
  height: 4em;
  right: 3em;
  top: 0.75em;
  width: 4em;
  display: none;
  z-index: 10002;
}

.navPanelToggle:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-transform: none !important;
  color: #fff;
  display: block;
  font-size: 16px;
  height: 2.25em;
  left: 0.5em;
  line-height: 2.25em;
  position: absolute;
  text-align: center;
  top: 0.5em;
  width: 3.5em;
}

@media screen and (max-width: 980px) {
  .navPanelToggle {
    display: block;
  }
}

@media screen and (max-width: 736px) {
  .navPanelToggle {
    right: 1em;
  }
}

.navPanelToggle *:before {
  color: #fff;
  display: block;
  font-size: 16px;
  height: 2.25em;
  left: 0.5em;
  line-height: 2.25em;
  position: absolute;
  text-align: center;
  top: 0.5em;
  width: 3.5em;
}

#navPanel {
  transform: translatex(20em);
  transition: transform 0.2s ease-in-out, visibility 0.2s ease-in-out;
  -webkit-overflow-scrolling: touch;
  visibility: hidden;
  overflow-y: auto;
  position: fixed;
  right: 0;
  top: 0;
  background: #1c1c1c;
  color: #fff;
  height: 100%;
  max-width: 80%;
  width: 20em;
  padding: 1.5em;
  text-transform: uppercase;
  z-index: 10003;
  display: none;
}

#navPanel.visible {
  transform: translatex(0);
  box-shadow: 0 0 1.5em 0 rgba(0, 0, 0, 0.2);
  visibility: visible;
}

#navPanel a:not(.close) {
  border-top: solid 1px rgba(255, 255, 255, 0.1);
  color: #fff;
  font-weight: 700;
  display: block;
  padding: 0.75em 0;
  text-decoration: none;
}

#navPanel a:not(.close):first-child {
  border: none;
}

#navPanel .close {
  text-decoration: none;
  transition: color 0.2s ease-in-out;
  -webkit-tap-highlight-color: transparent;
  border: 0;
  color: #484848;
  cursor: pointer;
  display: block;
  height: 4em;
  padding-right: 1.25em;
  position: absolute;
  right: 0;
  text-align: right;
  top: 0;
  vertical-align: middle;
  width: 5em;
}

#navPanel .close:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-transform: none !important;
  content: "\f00d";
  width: 3em;
  height: 3em;
  line-height: 3em;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  text-align: center;
}

#navPanel .close:hover {
  color: inherit;
}

@media screen and (max-width: 980px) {
  #navPanel {
    display: block;
  }
}

